        岳阳: [113.09, 29.37],
        长沙: [113, 28.21],
        衢州: [118.88, 28.97],
        廊坊: [116.7, 39.53],
        菏泽: [115.480656, 35.23375],
        合肥: [117.27, 31.86],
        武汉: [114.31, 30.52],
        大庆: [125.03, 46.58]
      }
      var data = []
      for (var key in geoCoordMap) {
        data.push({ name: key, value: geoCoordMap[key] })
      }
      return data
    },
    goToOther(index) {
      if (index === 0) {
        return
      } else if (index === 3) {
        this.$router.push('/visitrecord')
      } else if (index === 9) {
        this.$router.push('/anteroom')
      } else {
        this.$router.push('/' + this.path[index])
      }
    }
  }
}
</script>

